import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useBottomScrollListener } from "react-bottom-scroll-listener";
import { getInfiniteDocuments } from "../Firebase/DbFunc";
import ContentsCard from "./ContentsCard";
import { Title } from "./Typo";
import { Margin } from "./Utility";

const Box = styled.div`
  width: 100%;
  margin-top: 20px;
  margin-bottom: 40px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
`;

const CheckBoxLabel = styled.label``;

const CheckBoxInput = styled.input``;

const ContentsList = () => {
  const [contents, setContents] = useState([]);
  const [editorsPick, setEditorsPick] = useState(false);
  const [lastVisible, setLastVisible] = useState("");
  const getMoreContents = () => {
    if (lastVisible) {
      getInfiniteDocuments("urls", lastVisible, editorsPick)
        .then((res) => {
          setContents(contents.concat(res.contents));
          setLastVisible(res.lastVisible);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
  useBottomScrollListener(getMoreContents);
  useEffect(() => {
    getInfiniteDocuments("urls", null, editorsPick)
      .then((res) => {
        setContents(res.contents);
        setLastVisible(res.lastVisible);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [editorsPick]);
  return (
    <>
      <Title>
        {editorsPick ? "Editor's Pick 콘텐츠 리스트" : "전체 콘텐츠 리스트"}
      </Title>
      <Margin height={5}></Margin>
      <CheckBoxLabel>
        <CheckBoxInput
          type="checkbox"
          value="editorsPick"
          onChange={() => setEditorsPick((prev) => !prev)}
          checked={editorsPick === true}
        ></CheckBoxInput>
        Editor's Pick만 보기
      </CheckBoxLabel>
      <Box>
        {contents.map((content) => (
          <ContentsCard metadata={content} key={content.id}></ContentsCard>
        ))}
      </Box>
    </>
  );
};

export default ContentsList;
