import React, { useState } from "react";
import styled from "styled-components";
import { updateDocument } from "../Firebase/DbFunc";
import ContentsCard from "./ContentsCard";
import { T1 } from "./Typo";
import { Margin } from "./Utility";

const Box = styled.div`
  width: 100%;
  display: flex;
  margin-top: 20px;
  margin-bottom: 40px;
`;

const FormBox = styled.form`
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  margin-right: 20px;
`;

const TextInput = styled.input`
  margin-right: 10px;
  width: 200px;
  margin-bottom: 10px;
`;

const SubmitInput = styled.input`
  width: 200px;
  cursor: pointer;
`;

const RadioLabel = styled.label`
  margin-right: 10px;
`;

const RadioInput = styled.input``;

const ResultBox = styled.div`
  display: grid;
  width: 300px;
  margin-bottom: 20px;
`;

const ContentsUpdate = ({ metadata, closePopUp }) => {
  const [newMetadata, setNewMetadata] = useState(metadata);
  const updateMetadata = (event, type) => {
    event.preventDefault();
    const value = event.target.value;
    if (type === "url") {
      setNewMetadata({ ...newMetadata, url: value });
    } else if (type === "category") {
      setNewMetadata({ ...newMetadata, category: value.toLowerCase() });
    } else if (type === "time") {
      setNewMetadata({ ...newMetadata, time: value });
    } else if (type === "author") {
      setNewMetadata({ ...newMetadata, author: value });
    } else if (type === "channel") {
      setNewMetadata({ ...newMetadata, channel: value.toLowerCase() });
    } else if (type === "title") {
      setNewMetadata({ ...newMetadata, title: value });
    } else if (type === "desc") {
      setNewMetadata({ ...newMetadata, desc: value });
    } else if (type === "image") {
      setNewMetadata({ ...newMetadata, image: value });
    } else if (type === "editorsPick") {
      setNewMetadata((prev) => ({
        ...newMetadata,
        editorsPick: !prev.editorsPick,
      }));
    } else if (type === "editorsPickReason") {
      setNewMetadata({ ...newMetadata, editorsPickReason: value });
    } else if (type === "editorsName") {
      setNewMetadata({ ...newMetadata, editorsName: value });
    }
  };

  const onUpdate = async (event) => {
    event.preventDefault();
    const confirm = window.confirm("수정하시겠습니까?");
    if (confirm) {
      updateDocument("urls", metadata.id, newMetadata);
      closePopUp();
    } else {
      return;
    }
  };

  return (
    <Box>
      <FormBox onSubmit={onUpdate}>
        <T1>URL</T1>
        <TextInput
          type="text"
          placeholder="url 입력"
          value={newMetadata.url}
          onChange={(event) => updateMetadata(event, "url")}
        ></TextInput>
        <T1>카테고리</T1>
        <TextInput
          type="text"
          placeholder="카테고리 입력"
          value={newMetadata.category}
          onChange={(event) => updateMetadata(event, "category")}
        ></TextInput>
        <T1>이미지 주소</T1>
        <TextInput
          type="text"
          placeholder="이미지 주소 입력"
          value={newMetadata.image}
          onChange={(event) => updateMetadata(event, "image")}
        ></TextInput>
        <T1>시간</T1>
        <TextInput
          type="text"
          placeholder="시간 입력"
          value={newMetadata.time}
          onChange={(event) => updateMetadata(event, "time")}
        ></TextInput>
        <T1>제목</T1>
        <TextInput
          type="text"
          placeholder="제목 입력"
          value={newMetadata.title}
          onChange={(event) => updateMetadata(event, "title")}
        ></TextInput>
        <T1>설명</T1>
        <TextInput
          type="textarea"
          placeholder="설명 입력"
          value={newMetadata.desc}
          onChange={(event) => updateMetadata(event, "desc")}
        ></TextInput>
        <T1>창작자</T1>
        <TextInput
          type="text"
          placeholder="창작자 입력"
          value={newMetadata.author}
          onChange={(event) => updateMetadata(event, "author")}
        ></TextInput>
        <T1>채널</T1>
        <RadioLabel>
          <RadioInput
            type="radio"
            value="youtube"
            onChange={(event) => updateMetadata(event, "channel")}
            checked={newMetadata.channel === "youtube"}
          ></RadioInput>
          유튜브
        </RadioLabel>
        <RadioLabel>
          <RadioInput
            type="radio"
            value="etc"
            onChange={(event) => updateMetadata(event, "channel")}
            checked={newMetadata.channel === "etc"}
          ></RadioInput>
          기타
        </RadioLabel>
        <Margin height={15}></Margin>
        <T1>Editor's Pick</T1>
        <RadioLabel>
          <RadioInput
            type="checkbox"
            value="editorsPick"
            onChange={(event) => updateMetadata(event, "editorsPick")}
            checked={newMetadata.editorsPick === true}
          ></RadioInput>
          선정 여부
        </RadioLabel>
        <Margin height={10}></Margin>
        <TextInput
          type="text"
          placeholder="선정 이유"
          value={newMetadata.editorsPickReason}
          onChange={(event) => updateMetadata(event, "editorsPickReason")}
        ></TextInput>
        <T1>에디터</T1>
        <RadioLabel>
          <RadioInput
            type="radio"
            value="sowon"
            onChange={(event) => updateMetadata(event, "editorsName")}
            checked={newMetadata.editorsName === "sowon"}
          ></RadioInput>
          Sowon
        </RadioLabel>
        <RadioLabel>
          <RadioInput
            type="radio"
            value="bogus"
            onChange={(event) => updateMetadata(event, "editorsName")}
            checked={newMetadata.editorsName === "bogus"}
          ></RadioInput>
          Bogus
        </RadioLabel>
        <Margin height={20}></Margin>
        <SubmitInput type="submit" value="업데이트"></SubmitInput>
      </FormBox>
      <ResultBox>
        {metadata ? (
          <ContentsCard metadata={newMetadata} add={true}></ContentsCard>
        ) : null}
      </ResultBox>
    </Box>
  );
};

export default ContentsUpdate;
