import { andSearchDocument } from "../Firebase/DbFunc";

const getMetadata = (url, func) => {
  const data = {
    key: "5cc819fae5edbab33d7af928bc6bd6ef560d4b4081276",
    q: url,
  };
  fetch("https://api.linkpreview.net", {
    method: "POST",
    mode: "cors",
    body: JSON.stringify(data),
  })
    .then((res) => res.json())
    .then(async (response) => {
      const searchedData = await andSearchDocument(
        "urls",
        "url",
        response.url,
        "title",
        response.title
      );
      if (searchedData.length > 0) {
        alert("이미 존재하는 콘텐츠입니다.");
      } else {
        const { title, description, image, url } = response;
        func({
          title: title,
          desc: description,
          image: image,
          url: url,
        });
      }
    })
    .catch((err) => {
      throw err;
    });
};
export { getMetadata };
