import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Title, T1, T0 } from "./Typo";
import { searchDocument, updateDocument } from "../Firebase/DbFunc";
import { Margin } from "../Components/Utility";

const ListBox = styled.div`
  margin-top: 20px;
  width: 100%;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, 100px);
  margin-bottom: 20px;
`;

const CategoryBox = styled.button`
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid;
  cursor: pointer;
  color: ${(props) => (props.selected ? "white" : "black")};
  background-color: ${(props) => (props.selected ? "black" : "white")};
`;

const FormBox = styled.form`
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
`;

const TextInput = styled.input`
  margin-right: 10px;
  width: 200px;
  margin-bottom: 10px;
`;

const SubmitInput = styled.input`
  width: 200px;
  cursor: pointer;
`;

const FuncBtn = styled.button`
  width: 200px;
  height: 24px;
  border: 1px solid black;
`;

const CategoryList = () => {
  const [stage, setStage] = useState("view");
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [metadata, setMetadata] = useState(null);
  const [category, setCategory] = useState([]);
  const getCategory = async () => {
    const searchedCategory = await searchDocument("category");
    setCategory(searchedCategory.reverse());
  };
  const selectCategory = (categoryName) => {
    setSelectedCategory(categoryName);
    setStage("view");
  };

  const getCategoryMetadata = async () => {
    const categoryMetadata = await searchDocument(
      "category",
      "name",
      selectedCategory
    );
    setMetadata(categoryMetadata[0]);
  };

  useEffect(() => {
    getCategory();
  }, []);

  useEffect(() => {
    getCategoryMetadata();
  }, [selectedCategory, stage]);

  const updateMetadata = (event, type) => {
    console.log(metadata);
    event.preventDefault();
    const value = event.target.value;
    if (type === "desc") {
      setMetadata({ ...metadata, desc: value });
    } else if (type === "descUrl") {
      setMetadata({ ...metadata, descUrl: value });
    } else if (type === "bannerImg") {
      setMetadata({ ...metadata, bannerImg: value });
    } else if (type === "bannerTitle") {
      setMetadata({ ...metadata, bannerTitle: value });
    } else if (type === "bannerDesc") {
      setMetadata({ ...metadata, bannerDesc: value });
    } else if (type === "bannerCTA") {
      setMetadata({ ...metadata, bannerCTA: value });
    }
  };

  const onUpdate = async (event) => {
    event.preventDefault();
    const confirm = window.confirm("수정하시겠습니까?");
    if (confirm) {
      updateDocument("category", metadata.id, metadata);
      setStage("view");
    } else {
      return;
    }
  };

  return (
    <>
      <Title>카테고리</Title>
      <ListBox>
        {category.map((item) => (
          <CategoryBox
            key={category.id}
            selected={selectedCategory === item.name ? true : false}
            onClick={() => selectCategory(item.name)}
          >
            {item.name}
          </CategoryBox>
        ))}
      </ListBox>
      {stage === "update" ? (
        metadata ? (
          <FormBox onSubmit={onUpdate}>
            <T0>{metadata.name} 카테고리 정보 수정</T0>
            <Margin height={10}></Margin>
            <T1>카테고리 설명</T1>
            <TextInput
              type="text"
              placeholder="카테고리에 대한 설명"
              value={metadata.desc || ""}
              onChange={(event) => updateMetadata(event, "desc")}
            ></TextInput>
            <T1>카테고리 자세히 보기 링크</T1>
            <TextInput
              type="text"
              placeholder="카테고리 자세히 보기 링크"
              value={metadata.descUrl || ""}
              onChange={(event) => updateMetadata(event, "descUrl")}
            ></TextInput>
            <T1>카테고리 배너 이미지</T1>
            <TextInput
              type="text"
              placeholder="카테고리 배너 타이틀"
              value={metadata.bannerImg || ""}
              onChange={(event) => updateMetadata(event, "bannerImg")}
            ></TextInput>
            <T1>카테고리 배너 타이틀</T1>
            <TextInput
              type="text"
              placeholder="카테고리 배너 타이틀"
              value={metadata.bannerTitle || ""}
              onChange={(event) => updateMetadata(event, "bannerTitle")}
            ></TextInput>
            <T1>카테고리 배너 설명</T1>
            <TextInput
              type="text"
              placeholder="카테고리 배너 설명"
              value={metadata.bannerDesc || ""}
              onChange={(event) => updateMetadata(event, "bannerDesc")}
            ></TextInput>
            <T1>카테고리 배너 CTA</T1>
            <TextInput
              type="text"
              placeholder="카테고리 배너 설명"
              value={metadata.bannerCTA || ""}
              onChange={(event) => updateMetadata(event, "bannerCTA")}
            ></TextInput>
            <SubmitInput type="submit" value="수정"></SubmitInput>
          </FormBox>
        ) : null
      ) : null}
      <FuncBtn
        onClick={() =>
          setStage((prev) => (prev === "update" ? "view" : "update"))
        }
      >
        {stage === "update" ? "취소" : "카테고리 정보 수정"}
      </FuncBtn>
    </>
  );
};

export default CategoryList;
