import React, { useState } from "react";
import styled from "styled-components";
import { addDocument } from "../Firebase/DbFunc";
import { getMetadata } from "../Functions/Functions";
import ContentsCard from "./ContentsCard";
import { T1, Title } from "./Typo";
import { Margin } from "./Utility";
import { previousData } from "../Assets/PreviousData/array";

const Box = styled.div`
  width: 100%;
  display: flex;
  margin-top: 20px;
  margin-bottom: 40px;
`;

const LeftBox = styled.div`
  display: flex;
  flex-direction: column;
  margin-right: 30px;
`;

const FormBox = styled.form`
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
`;

const TextInput = styled.input`
  margin-right: 10px;
  width: 200px;
  margin-bottom: 10px;
`;

const SubmitInput = styled.input`
  width: 200px;
  cursor: pointer;
`;

const RadioLabel = styled.label`
  margin-right: 10px;
`;

const RadioInput = styled.input``;

const ResultBox = styled.div`
  display: grid;
  width: 300px;
  margin-bottom: 20px;
`;

const ContentsAdd = () => {
  const [url, setUrl] = useState("");
  const [metadata, setMetadata] = useState(null);

  const setDefaultMeta = (value) => {
    setMetadata({ ...value, channel: "youtube", createdAt: new Date() });
  };

  const onUrlChange = (event) => {
    const value = event.target.value;
    setUrl(value);
  };

  const uploadPreviousContents = () => {
    const reversedPreviousData = previousData.reverse();
    reversedPreviousData.map((data, index) => {
      const { image, title, desc, url, category, time, author, channel } = data;
      setTimeout(() => {
        addDocument("urls", {
          image: image ? image : null,
          title: title ? title : null,
          desc: desc ? desc : null,
          url: url ? desc : null,
          category: category ? category.toLowerCase() : null,
          time: time ? time : null,
          author: author ? author : null,
          channel: channel ? channel : null,
          createdAt: new Date(),
        });
      }, index * 1000);
    });
  };

  const updateMetadata = (event, type) => {
    event.preventDefault();
    const value = event.target.value;
    if (type === "category") {
      setMetadata({ ...metadata, category: value.toLowerCase() });
    } else if (type === "time") {
      setMetadata({ ...metadata, time: value });
    } else if (type === "author") {
      setMetadata({ ...metadata, author: value });
    } else if (type === "channel") {
      setMetadata({ ...metadata, channel: value.toLowerCase() });
    } else if (type === "title") {
      setMetadata({ ...metadata, title: value });
    } else if (type === "desc") {
      setMetadata({ ...metadata, desc: value });
    } else if (type === "image") {
      setMetadata({ ...metadata, image: value });
    }
  };

  const onSearch = async (event) => {
    event.preventDefault();
    const valid = url.length > 0;
    if (valid) {
      getMetadata(url, setDefaultMeta);
    } else {
      return;
    }
  };

  const onUpload = async (event) => {
    event.preventDefault();
    addDocument("urls", metadata);
    setUrl("");
  };

  return (
    <>
      <Title>콘텐츠 추가</Title>
      <Box>
        <LeftBox>
          <FormBox onSubmit={onSearch}>
            <TextInput
              type="text"
              placeholder="URL 입력"
              value={url}
              onChange={onUrlChange}
            ></TextInput>
            <SubmitInput
              type="submit"
              value="검색"
              valid={url.length > 0}
            ></SubmitInput>
          </FormBox>
          {metadata ? (
            <>
              <FormBox onSubmit={onUpload}>
                <T1>카테고리</T1>
                <TextInput
                  type="text"
                  placeholder="카테고리 입력"
                  value={metadata.category}
                  onChange={(event) => updateMetadata(event, "category")}
                ></TextInput>
                <T1>이미지 주소</T1>
                <TextInput
                  type="text"
                  placeholder="이미지 주소 입력"
                  value={metadata.image}
                  onChange={(event) => updateMetadata(event, "image")}
                ></TextInput>
                <T1>시간</T1>
                <TextInput
                  type="text"
                  placeholder="시간 입력"
                  value={metadata.time}
                  onChange={(event) => updateMetadata(event, "time")}
                ></TextInput>
                <T1>제목</T1>
                <TextInput
                  type="text"
                  placeholder="제목 입력"
                  value={metadata.title}
                  onChange={(event) => updateMetadata(event, "title")}
                ></TextInput>
                <T1>설명</T1>
                <TextInput
                  type="textarea"
                  placeholder="설명 입력"
                  value={metadata.desc}
                  onChange={(event) => updateMetadata(event, "desc")}
                ></TextInput>
                <T1>창작자</T1>
                <TextInput
                  type="text"
                  placeholder="창작자 입력"
                  value={metadata.author}
                  onChange={(event) => updateMetadata(event, "author")}
                ></TextInput>
                <T1>채널</T1>
                <RadioLabel>
                  <RadioInput
                    type="radio"
                    value="youtube"
                    onChange={(event) => updateMetadata(event, "channel")}
                    checked={metadata.channel === "youtube"}
                  ></RadioInput>
                  유튜브
                </RadioLabel>
                <RadioLabel>
                  <RadioInput
                    type="radio"
                    value="etc"
                    onChange={(event) => updateMetadata(event, "channel")}
                    checked={metadata.channel === "etc"}
                  ></RadioInput>
                  기타
                </RadioLabel>
                <Margin height={15}></Margin>
                {metadata ? (
                  <SubmitInput type="submit" value="업로드"></SubmitInput>
                ) : null}
              </FormBox>
            </>
          ) : null}
        </LeftBox>
        <ResultBox>
          {metadata ? (
            <ContentsCard metadata={metadata} add={true}></ContentsCard>
          ) : null}
        </ResultBox>
        {/* <button onClick={uploadPreviousContents}>기존 콘텐츠 추가</button> */}
      </Box>
    </>
  );
};

export default ContentsAdd;
