import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Title, T0, T2 } from "./Typo";
import { searchDocument } from "../Firebase/DbFunc";

const Box = styled.div`
  margin-top: 20px;
  width: 100%;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, 100px);
  margin-bottom: 40px;
`;

const CategoryBox = styled.div`
  height: 100px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
`;

const ContentsDashBoard = () => {
  const [category, setCategory] = useState([]);
  const getCategory = async () => {
    const all = await searchDocument("urls");
    const dog = await searchDocument("urls", "category", "dog");
    const cat = await searchDocument("urls", "category", "cat");
    setCategory([
      { categoryName: "all", categoryCount: all.length },
      { categoryName: "dog", categoryCount: dog.length },
      { categoryName: "cat", categoryCount: cat.length },
    ]);
  };

  useEffect(() => {
    getCategory();
  }, []);
  return (
    <>
      <Title>콘텐츠 현황</Title>
      <Box>
        {category.map((item) => (
          <CategoryBox>
            <T0>{item.categoryName}</T0>
            <T2>{item.categoryCount}개</T2>
          </CategoryBox>
        ))}
      </Box>
    </>
  );
};

export default ContentsDashBoard;
